const InfoCards = ({ className, children }) => (
    <div className={`${className} flex flex-row space-x-8 justify-around`}>
        {children}
    </div>
)

const InfoCard = ({ info, children, title }) => (
	<div className='text-center bg-blue-100 rounded p-2 px-4 hover:bg-blue-200 transition-all'>
        
        <div className="text-sm mb-1">
            {title}
        </div>

        <div className="font-medium">
            {children || info}
        </div>

	</div>
)


export {
    InfoCards,
    InfoCard
}

