
const TeamCards = ({ className, people, isOwners }) => (
    <div className={`${className} flex md:flex-row justify-around items-start flex-wrap max-w-md`}>
        {people.map((person, index) => (
            <TeamCard
                name={person.name}
                title={person.title}
                image={person.image}
                isOwner={isOwners}
            ></TeamCard>
        ))}
    </div>
)

const TeamCard = ({ name, title, image, isOwner }) => (
    <div className={`${isOwner ? 'w-40' : 'w-36'} p-4`}>
    
        <div className="group flex flex-col text-center">

            <div className="overflow-hidden rounded-full inline-block aspect-square border-blue-100 border-8">
                <img src={image} className="group-hover:scale-110 aspect-square object-cover transition-all"></img>
            </div>

            <div className={`${isOwner ? 'text-xl' : 'text-xl'} font-bold mt-2`}>
                {name}
            </div>

            <div className={`${isOwner ? '' : ''} text-gray-400`}>
                {title}
            </div>

        </div>

    </div>
)


export default TeamCards

