import { Link } from "preact-router/match"
import { MyLink } from "../../components/navigation/myLink"

const MainCards = ({ className, links }) => (
    <div className={`${className} space-y-12 flex flex-col items-center justify-center w-full mx-auto`}>
        {links.map((link, index) => (
            <MainCard
                title={link.title}
                image={link.image}
                url={link.url}
                description={link.description}
                isEven={index % 2 == 0}
            />
        ))}
    </div>
)

const MainCard = ({ title, image, url, description, isEven }) => (
    <div className={`${isEven ? 'sm:flex-row-reverse' : 'sm:flex-row'} flex-col flex sm:h-64 justify-center`}>
        
        <MyLink href={url} className="overflow-hidden rounded-lg inline-block">
            <img src={image} className="hover:scale-110 transition-all object-cover h-full aspect-video"></img>
        </MyLink>

        <div className="h-8 sm:w-8 grow-0 shrink-0"></div>
        
        <div className="sm:max-w-sm flex flex-col justify-center">
                
            <MyLink href={url} className="group">
                <div className="font-bold text-4xl group-hover:underline">
                    {title}
                </div>
            </MyLink>

            {description &&
                <div className="mt-2 text-lg">
                    {description}
                </div>
            }

        </div>

    </div>
)

export default MainCards

