import { Link } from "preact-router/match"
import { MyLink } from "../../components/navigation/myLink"

const LinkCards = ({ className, links }) => (
    <div className={`${className} flex md:flex-row flex-col justify-around items-center flex-wrap`}>
        {links.map((link, index) => (
            <LinkCard
                title={link.title}
                image={link.image}
                url={link.url}
                description={link.description}
            />
        ))}
    </div>
)

const LinkCard = ({ title, image, url, description }) => (
    <div className='p-4 text-center max-w-md'>
        
        <MyLink href={url} className="group">
            <div className="font-bold text-xl group-hover:underline">
                {title}
            </div>

            <div className="overflow-hidden rounded-md mt-2 inline-block">
                <img src={image} className="group-hover:scale-110 transition-all"></img>
            </div>
        </MyLink>

        {description &&
            <div className="mt-2">
                {description}
            </div>
        }

    </div>
)

export default LinkCards

