import { Link } from "preact-router/match"
import { MyLink } from "../../components/navigation/myLink"

const Banner = ({ title, subtitle, url, className }) => {

    const isUrl = (url && url.length > 0)

    return (
        <MyLink href={url}>
            <section className={`${className} ${isUrl ? 'hover:bg-yellow-400 hover:py-4' : 'cursor-default'} text-center rounded-xl py-3 px-4 bg-yellow-300 transition-all max-w-3xl`}>

                <div className="font-bold text-lg">
                    {title}
                </div>

                <div className="">
                    {subtitle}
                </div>

            </section>
        </MyLink>
    )
}

export default Banner

